<template>
  <div>
    <div style="width:500px;overflow: auto">
      <s-table
          :data-source="data"
          :sort-list="sortList"
          :rowSelection="rowSelection"
          :sort-config="{
            push: ['ds']
          }"
          :cellWidth.sync="config"
          :pagination="false"
          draggable
          @change="onDrag"
      >
        <a-table-column key="index" title="标题1" :width="config.index">
          <div
              slot="filterDropdown"
          >231321</div>
          <template slot-scope="record">
            {{record.date}}
          </template>
        </a-table-column>
        <a-table-column key="type" title="标题222" :width="config.type">
          <template slot-scope="row, record, index">
            {{row.type}}=={{index}}
          </template>
        </a-table-column>
        <a-table-column key="name" title="标题3" :width="config.name"></a-table-column>
        <a-table-column key="sort" title="标题4" :width="config.sort"></a-table-column>
        <a-table-column key="action"  title="标题5" ></a-table-column>
      </s-table>
      <a-table
          :showHeader="false"
          :pagination="false"
          :data-source="totalData"
          :rowSelection="rowSelection"
          style="width:1000px;"
          draggable
          @change="onDrag"
          class="footer-table"
      >
        <a-table-column key="index" title="标题1" :width="config.index" class="table-drag-index" :customCell="() => { return {attrs: { colSpan: 3 }}}">
          <template slot-scope="record">
            {{record.index}}
          </template>
        </a-table-column>
        <a-table-column key="sort" data-index="sort" title="标题4" :width="config.sort"></a-table-column>
        <a-table-column key="ds" data-index="ds" title="标题5" :width="config.ds"></a-table-column>
        <a-table-column title="标题5" :width="config.ds"></a-table-column>
      </a-table>
    </div>
    <a-button @click="exchangeList"></a-button>
    <s-table
        :data-source="data"
        :rowSelection="rowSelection"
        :columns="columns"
        :sort-list="sortList"
        style="width:1000px;"
        draggable
        @change="onDrag"
        ref="dragWidthHeight2"
    >
      <div
          slot="filterDropdownSpe"
      >231321</div>
    </s-table>
  </div>
</template>

<script>
import { STable, TableSelection } from '@handday/components'
export default {
  name: 'StableDrag',
  mixins: [TableSelection],
  components: {
    STable
  },
  data () {
    return {
      data: [],
      sortList: [],
      tagsFilterDropdownVisible: false,
      columns: [
        { dataIndex: 'index', key: 'index', before: true, title: '索引' },
        { dataIndex: 'type', key: 'type', title: '类型', scopedSlots: {
            filterDropdown: 'filterDropdownSpe'
          }
        },
        { dataIndex: 'note', key: 'note', title: '记录', scopedSlots: {
            filterDropdown: 'filterDropdownSpe'
          }
        }
      ],
      totalData: [
        { index: '统计', type: '空', name: '名称', sort: '排序', ds: '空' }
      ],
      config: {
        index: 100,
        type: 100,
        name: 100,
        sort: 100,
        ds: 200
      }
    }
  },
  created() {
    for (let i = 0; i < 10; i++) {
      this.data.push({
        key: i,
        date: new Date().getTime(),
        amount: `amount${i}`,
        type: `type${i}`,
        note: `note${i}`,
        date1: `date1${i}`,
        amount2: `amount2${i}`,
        type3: `type3${i}`
      })
    }
  },
  methods: {
    exchangeList () {
      this.sortList = [
        { key: 'name' },
        { key: 'ds' },
        { key: 'sort' },
        { key: 'title' },
        { key: 'action' }
      ]
    },
    onDrag (result) {
      // this.config[result.col.key] = result.width
    },
    customerCell (record, index) {
      console.log(record, index)
      return {
        attrs: {
          colSpan: 3
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.footer-table{
  /deep/ .ant-table-selection-column{display: none;}
}
</style>
